.header-wrapper {
  width: 80vw;
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    font-size: 1rem;
    text-align: center;
    @include media(">=phone", "<lgphone") {
      font-size: 14px;
    }
    @include media(">=lgphone", "<tablet") {
      font-size: 16px;
    }
    @include media(">=tablet", "<desktop") {
      font-size: 16px;
    }
  }
  .heading-wrapper {
    h1 {
      font-size: 4rem;
      text-align: center;
      line-height: 20px;
      @include media(">=phone", "<lgphone") {
        font-size: 1.5rem;
        line-height: 10px;
      }
      @include media(">=lgphone", "<tablet") {
        font-size: 1.5rem;
        line-height: 10px;
      }
      @include media(">=tablet", "<desktop") {
        font-size: 2.5rem;
      }
    }
  }
  p {
    width: 50%;
    text-align: center;
    overflow: hidden;
    @include media(">=phone", "<lgphone") {
      font-size: 12px;
      width: 100%;
    }
    @include media(">=lgphone", "<tablet") {
      font-size: 12px;
      width: 100%;
    }
    @include media(">=tablet", "<desktop") {
      font-size: 14px;
      width: 100%;
    }
  }
}

.header-icons {
  display: flex;
}

.header-icon {
  margin: 10px;
  & img {
    width: 50px;
    border-radius: 50%;
    @include media(">=phone", "<lgphone") {
      width: 30px;
    }
    @include media(">=lgphone", "<tablet") {
      width: 30px;
    }
  }
}

.gif {
  position: absolute;
  top: -30px;
  z-index: -1;
  max-width: 100%;
  @include media(">=phone", "<lgphone") {
    top: 60px;
  }
  @include media(">=lgphone", "<tablet") {
    top: 60px;
  }
}